/* rubik-regular - latin */
@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  src: url('../../public/fonts/rubik/rubik-v21-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../../public/fonts/rubik/rubik-v21-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../../public/fonts/rubik/rubik-v21-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../../public/fonts/rubik/rubik-v21-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../../public/fonts/rubik/rubik-v21-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../../public/fonts/rubik/rubik-v21-latin-regular.svg#Rubik') format('svg'); /* Legacy iOS */
}

html {
    scroll-behavior: smooth;
}

div {
    top: 0;
    left: 0;
    border: 0;
    padding: 0;
    margin: 0;
}

a, button {
    text-decoration: none;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

.window-container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
}

.navbar {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5%;
    width: 35%;
    height: 40%;
}

.nav-active-text {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: min(0.9vw, 2.1vh);
    line-height: 24px;
    letter-spacing: 0.345em;
    background-color: unset;
    color: rgba(0, 0, 0, 0.7);
    border: 0;
}

.nav-text-buttons {
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: min(0.9vw, 2.1vh);
    line-height: 24px;
    letter-spacing: 0.345em;
    background-color: unset;
    color: rgba(0, 0, 0, 0.5);
    border: 0;
}

.nav-buttons {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
}

.nav-text-buttons:hover {
    text-decoration: underline;
    text-underline-offset: 0.5rem;
}

.nav-active-buttons {
    display: flex;
    align-items: center;
    margin-right: 2%;
    width: 100%;
    height: 100%;
    background: rgba(255, 253, 253, 0.7);
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.25);
    border-radius: 45px;
}

.logo {
    z-index: 1;
    padding: 0.2% 1%;
}

.title {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 96px;
    line-height: 116px;

    color: #FFFDFD;

    text-shadow: 9px 4px 5px rgba(0, 0, 0, 0.25);
}

.description {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 36px;
    line-height: 44px;

    /* identical to box height */

    color: #FFFDFD;
}

.learn-more-btn {
    border: none;

    background: #FFFDFD;
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.25);
    border-radius: 54px;

    padding-top: 1%;
    padding-bottom: 1%;
}

.btn-text {
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 24px;
    line-height: 39px;
    align-items: center;
    display:flex;
    place-content: center;

    /* identical to box height */
    letter-spacing: 0.07em;

    color: #515151;
}

h1 {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 54px;
    letter-spacing: 0.07em;

    color: #FFFFFF;

}

h2 {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 33px;
    letter-spacing: 0.07em;

    color: #FFFDFD;
}

.whoAreWe {
    width: 100%;
    height: 70%;
    display: flex;
    position: relative;
    background: #F7F2F3;
}

.meet-our-team {
    height: 100%;
    display: flex;
    position: relative;
    background: #F7F2F3;
    place-content: center;
    flex-direction: column;
    align-items: flex-start;
    padding: 0 7%;
}

.team-text {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.07em;
    padding-bottom: 3%;

    color: #000000;
}

.headshots {
    display: flex;
    position: relative;
    width: 100%;
    place-content: space-between;
}

.faq {
    background: #181D25;
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    place-content: center;
    align-items: center;
    flex-direction: column;
}

.faq-text {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 48px;
    line-height: 65px;
    letter-spacing: 0.15em;

    color: #FFFFFF;
}

.QA-full {
    padding: 2% 5%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 10%;
    position: relative;
    flex-direction: row;
}

.QA-columns {
    display: flex;
    position: relative;
    flex-direction: column;
}

.QA-list {
    display: flex;
    position: relative;
    flex-direction: row;
    align-items: flex-start;
}

.list-bullet {
    object-fit: contain;
    width: 8px;
    padding-top: 3.5%;
    padding-right: 2%;
}

.QA {
    display: flex;
    position: relative;
    flex-direction: column;
    padding-bottom: 5%;
}

.Question {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 250%;

    /* identical to box height, or 50px */
    letter-spacing: 0.07em;

    color: #FFFDFD;
}

.Answer {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;

    /* or 24px */

    color: #D8D8D8;
}

.footer {
    display: flex;
    position: relative;
    height: 100%;
    width: 100%;
    background: #000509;
}
